<template>
	<div id="merchant-index">
		<public-header title="商户管理"></public-header>
		<div class="content">
			<div class="group">
				<van-row gutter="20">
					<van-col span="8" v-if="util.checkperm('merchant.store')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/merchant/store'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-shop c-primary"></i>
							</div>
							<div class="bottom">
								<span>商户列表</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkperm('merchant.activity')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/merchant/activity/list'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-presentfill c-danger"></i>
							</div>
							<div class="bottom">
								<span>商户活动</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkperm('merchant.getcash')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/merchant/getcash'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-sponsor"></i>
							</div>
							<div class="bottom">
								<span>提现申请</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8"  v-if="util.checkperm('merchant.current')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/merchant/current'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-money c-primary"></i>
							</div>
							<div class="bottom">
								<span>账户明细</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkperm('merchant.settle')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/merchant/settle'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-home c-info"></i>
							</div>
							<div class="bottom">
								<span>商户入驻</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkperm('merchant.storage')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/merchant/storage'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-deletefill"></i>
							</div>
							<div class="bottom">
								<span>回收站</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8"  v-if="util.checkperm('merchant.report')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/merchant/report'})" class="merchant-item">
							<div class="top">
								<i class="icon icon-survey"></i>
							</div>
							<div class="bottom">
								<span>投诉列表</span>
							</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {

			}
		},
		components: {
			publicHeader,
		},
		methods: {

		}
	}
</script>

<style>
	#merchant-index .content{
		background-color: #fff;
	}
	#merchant-index .content .group{
		padding: 20px 15px;
	}
	#merchant-index .content .group .van-row{
		text-align: center;
	}
	#merchant-index .content .group .merchant-item{
		margin-bottom: 20px;
	}
	#merchant-index .content .group .merchant-item .top{
		padding: 10px;
	}
	#merchant-index .content .group .merchant-item .top .icon{
		font-size: 40px;
	}
	#merchant-index .content .group .merchant-item .bottom{
		font-size: 14px;
		color: #7D8484;
	}
	#merchant-index .content .group .merchant-item .top .icon-sponsor{
		color: #26A0DF;
	}
	#merchant-index .content .group .merchant-item .top .icon-deletefill{
		color: #5C5C5C;
	}
	#merchant-index .content .group .merchant-item .top .icon-survey{
		color: #F4D81C;
	}
</style>


